<template>
  <v-card flat style="background: #F4F7FC">
    <v-form lazy-validation @submit.prevent="submitLogin" ref="loginForm" v-model="valid">
      <v-icon @click="closeButton" style="float:right">mdi-close</v-icon>
      <br />
      <br />
      <div class="pa-2">
        <div style="display: flex;justify-content: center;">
          <img :src="require('@/assets/logo.png')" alt />
        </div>
        <br />
        <br />
        <p>Email Pegawai</p>
        <v-text-field
          v-model="loginForm.email"
          class="login-form"
          label="Email Pegawai"
          :rules="emailRules"
          single-line
          outlined
        ></v-text-field>

        <p>Password</p>
        <v-text-field
          v-model="loginForm.password"
          class="login-form"
          label="Password"
          :rules="passwordRules"
          type="password"
          single-line
          outlined
        ></v-text-field>
      </div>

      <v-card-actions>
        <v-btn type="submit" block x-large color="primary" class="white--text">Masuk</v-btn>
      </v-card-actions>
    </v-form>
  </v-card>
</template>
<script>
import ApiClient from "@/client/index.js";
import store from "@/store/index.js";
export default {
  data() {
    return {
      valid: false,
      errors: [],
      loginForm: {
        email: "",

        password: ""
      }
    };
  },
  computed: {
    emailRules() {
      return [
        v => !!v || "E-mail is required",
        v => /.+@.+/.test(v) || "E-mail must be valid",
        !this.errors.includes("email") || "E-mail is wrong"
      ];
    },
    passwordRules() {
      return [
        v => !!v || "Password is required",
        v => v.length >= 6 || "Password must at least be 6 characters",
        !this.errors.includes("password") || "Password is wrong"
      ];
    }
  },
  methods: {
    closeButton() {
      store.commit("hideLogin");
    },
    submitLogin() {
      ApiClient.login(this.loginForm.email, this.loginForm.password)
        .then(() => this.$router.push("profile"))
        .catch(error => {
          console.log(error.response);
          console.log(error.response.data.errors);
          const errorDesc = error.response.data.errors;
          if (error.response.status === 401) {
            let totalErrors = [];
            errorDesc.forEach(
              item => (totalErrors = [...totalErrors, ...Object.keys(item)])
            );
            this.errors = [...totalErrors];
            this.$refs.loginForm.validate();
          } else {
            this.errors = [{ all: "Terjadi kesalahan sistem." }];
          }
        });
    }
  },
  watch: {
    loginForm: {
      handler() {
        this.errors = [];
        this.$refs.loginForm.resetValidation();
      },
      deep: true
    }
  }
};
</script>
<style lang="scss">
.login-form {
  .v-input__slot {
    background: white !important;
  }
}
</style>
