import axios from "axios";
import store from "@/store/index.js";

/**
 * Create a new Axios client instance
 * @see https://github.com/mzabriskie/axios#creating-an-instance
 */
const getClient = (baseUrl = null) => {
  const options = {
    baseURL: baseUrl,
    headers: {
      Accept: "application/json"
    }
  };

  const client = axios.create(options);

  // Add a request interceptor
  client.interceptors.request.use(
    requestConfig => {
      if (store.state.isAuthenticated) {
        requestConfig.headers = {
          ...requestConfig.headers,
          Authorization: `Bearer ${store.state.accessToken}`
        };
      }
      return requestConfig;
    },
    requestError => {
      return Promise.reject(requestError);
    }
  );

  // Add a response interceptor
  client.interceptors.response.use(
    response => response,
    error => {
      return Promise.reject(error);
    }
  );

  return client;
};

class ApiClient {
  constructor(baseUrl) {
    this.client = getClient(baseUrl);
  }

  get(url, conf = {}) {
    return this.client
      .get(url, conf)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  }

  delete(url, conf = {}) {
    return this.client
      .delete(url, conf)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  }

  head(url, conf = {}) {
    return this.client
      .head(url, conf)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  }

  options(url, conf = {}) {
    return this.client
      .options(url, conf)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  }

  post(url, data = {}, conf = {}) {
    return this.client
      .post(url, data, conf)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  }

  put(url, data = {}, conf = {}) {
    return this.client
      .put(url, data, conf)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  }

  patch(url, data = {}, conf = {}) {
    return this.client
      .patch(url, data, conf)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  }
}

export { ApiClient };

const BASE_URL = process.env.VUE_APP_URL + "/";

export const client = new ApiClient(BASE_URL);

export default {
  login(email, password) {
    return client
      .post("login", { email, password })
      .then(response => {
        const { token, user } = response.data;
        store
          .dispatch("userLoggedIn", { token, user })
          .then(() => Promise.resolve(response));
      })
      .catch(error => Promise.reject(error));
  },
  newestPolicy() {
    return client
      .get("policy/allnofilter")
      .then(response => Promise.resolve(response.data.response_data))
      .catch(error => Promise.reject(error));
  },
  profile(username) {
    return client
      .get("users" + username)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  },
  quizzAll(page = 1) {
    // TODO change to proper query build
    const userId = store.state.userObject.id;

    return client
      .get(
        `quizz/user?status=0&sortBy=name&sorting=desc&size=10&user_id=${userId}&page=${page}`
      )
      .then(response => Promise.resolve(response.data.response_data))
      .catch(error => Promise.reject(error));
  },
  quizzHistory(page = 1) {
    // TODO change to proper query build
    const userId = store.state.userObject.id;

    return client
      .get(
        `quizz/user?status=2&sortBy=name&sorting=desc&size=10&user_id=${userId}&page=${page}`
      )
      .then(response => Promise.resolve(response.data.response_data))
      .catch(error => Promise.reject(error));
  },
  getPdf(path) {
    return client
      .post("/policy/pdf", { link_policy: path }, { responseType: "blob" })
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  },
  getPolicy(page, sortBy, sorting, search, filterData) {
    return client
      .get(`/policy/all${page}`, {
        params: {
          sortBy: sortBy,
          sorting: sorting,
          search: search,
          filter: filterData
        }
      })
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  },
  getPolicyDetail(id) {
    return client
      .get(`/policy/detail/${id}`)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  },
  getCategory() {
    return client
      .get("/categories")
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  },
  watermarkPdf(link_policy, nik) {
    return client
      .post(
        "/watermark",
        { link_policy: link_policy, nik: nik },
        { responseType: "blob" }
      )
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error));
  }
};
