    <v-container fluid>
      <v-row align="center" justify="space-between" style="margin">
        <v-col cols="3" class="navbar-left">
          <router-link :to="store.state.isAuthenticated ? {name: 'profile'} : {name: 'home'}">
            <div class="logo">
              <img :src="require('@/assets/logo.png')" alt />
            </div>
            <span class="new-policy">New Policy System</span>
          </router-link>
        </v-col>
        <v-col class="navbar-search">
          <div class="r-searchField">
            <div class="r-icon">
              <img :src="require('@/assets/search_24px.svg')" alt />
            </div>
            <input type="text" v-model="search" @keyup.enter="searchData()" />
            <div class="search-button" @click="searchData()">Search</div>
          </div>
        </v-col>
        <!-- Non logged in right menu -->
        <v-col cols="2" class="navbar-right" v-if="!store.state.isAuthenticated">
          <v-menu offset-y>
            <template v-slot:activator="{ on }">
              <v-layout class="justify-end mr-3" v-on="on">
                <span>Menu</span>
                <img class="ml-3" :src="require('@/assets/arrow_drop_down_circle_24px.svg')" alt />
              </v-layout>
            </template>
            <v-list class="public-menu" nav>
              <v-list-item link v-for="(item, index) in menuItems" :key="index" :to="item.route">
                <v-list-item-title>{{ item.title }}</v-list-item-title>
              </v-list-item>
              <v-list-item>
                <v-btn
                  outlined
                  color="primary"
                  class="text-none"
                  block
                  large
                  @click="showLogin"
                >Masuk</v-btn>
              </v-list-item>
            </v-list>
          </v-menu>
        </v-col>

        <!-- TODO STUB logged in right top bar -->
        <v-col cols="2" class="navbar-right" v-if="!!store.state.isAuthenticated">
          <v-layout>
            <v-layout>
              <v-icon class="pr-2">mdi-email-outline</v-icon>
              <a>Inbox</a>
            </v-layout>
            <v-layout>
              <v-icon class="pr-2">mdi-account-outline</v-icon>
              <a>{{ store.state.userObject.name }}</a>
            </v-layout>
          </v-layout>
        </v-col>
        <!--
    <router-link :to="{ name: 'home' }">
      <h1>Policy System</h1>
        </router-link>-->
      </v-row>
    </v-container>
  </v-app-bar>
</template>
<script>
import store from "@/store/index.js";
export default {
  data: () => ({
    search: localStorage.getItem("search"),
    menuItems: [
      { title: "Ketentuan", route: { name: "policy" } },
      { title: "Bantuan", route: { name: "help" } }
    ]
  }),
  computed: {
    // TODO probably isn't the right way to reference store on template..
    store() {
      return store;
    }
  },
  methods: {
    searchData() {
      localStorage.setItem("search", this.search);
      if (this.search === null || this.search === "") {
        this.$router.push({ name: "policy" });
      } else {
        this.$router.push({
          name: "policySearch",
          params: { search: this.search }
        });
      }
    },
    click() {
      return;
    },
    showLogin() {
      store.commit("showLogin");
    }
  }
};
</script>
