/* eslint-disable prettier/prettier */
import Vue from "vue";
import VueRouter from "vue-router";
import Welcome from "@/views/Welcome.vue";

Vue.use(VueRouter);

function loadView(view) {
  return () =>
    import(
      /* webpackChunkName: "view-[request]" */ `../components/${view}.vue`
    );
}
const routes = [
  {
    path: "/",
    name: "home",
    component: Welcome
  },
  {
    name: "preview",
    path: "/preview",
    component: loadView("kebijakanPreview/main")
  },
  {
    path: "/about",
    name: "about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue")
  },
  {
    path: "/policy",
    name: "policy",
    component: loadView("policyDetail/main")
  },
  {
    path: "/policy/:id",
    name: "policyDetail",
    component: loadView("policyDetail/main")
  },
  {
    path: "/policy/search/:search",
    name: "policySearch",
    component: loadView("policyDetail/main")
  },
  {
    path: "/profile",
    name: "profile",
    component: () => import("@/views/Profile.vue")
  },
  {
    path: "/quiz/detail",
    name: "quiz-detail",
    props: true,
    component: () => import("@/views/QuizDetail.vue")
  },
  {
    path: "/quiz/solution",
    name: "quiz-solution",
    props: true,
    component: () => import("@/views/QuizSolution.vue")
  },
  {
    path: "/quiz",
    name: "quiz",
    component: () => import("@/views/Quiz.vue")
  },
  // {
  //   path: "/news",
  //   name: "news",
  //   component: () => import("@/views/News.vue")
  // },
  {
    path: "/help",
    name: "help",
    component: () => import("@/views/Help.vue")
  },
  {
    path: "/*",
    redirect: "/"
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.ROUTER_BASE_PATH,
  routes
});

export default router;
