import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    isLogin: false,
    minimizeSideBar: false,
    policyDetailId: null,
    // TODO split/organize user states
    isAuthenticated: false,
    accessToken: null,
    userObject: null
  },
  mutations: {
    setPolicyDetailId(state, source) {
      state.policyDetailId = source;
    },
    resetPolicyDetailid(state) {
      state.policyDetailId = null;
    },
    toggleSideBar(state) {
      state.minimizeSideBar = !state.minimizeSideBar;
    },
    minimizeSideBar(state) {
      state.minimizeSideBar = true;
    },
    maximizeSideBar(state) {
      state.minimizeSideBar = false;
    },
    showLogin(state) {
      state.isLogin = true;
    },
    hideLogin(state) {
      state.isLogin = false;
    },
    loadUserTokenObject(state, payload) {
      state.isAuthenticated = true;
      state.accessToken = payload.token;
      state.userObject = payload.user;
    },
    wipeUserTokenObject(state) {
      state.isAuthenticated = false;
      state.accessToken = null;
      state.userObject = null;
    }
  },
  actions: {
    initializeStore(state) {
      if (localStorage.getItem("accessToken")) {
        Promise.all([
          localStorage.getItem("accessToken"),
          JSON.parse(localStorage.getItem("userObject"))
        ]).then(([token, user]) => {
          state.commit("loadUserTokenObject", { token, user });
        });
      }
    },
    userLoggedIn(state, payload) {
      localStorage.setItem("accessToken", payload.token);
      localStorage.setItem("userObject", JSON.stringify(payload.user));
      state.commit("loadUserTokenObject", payload);
    },
    userLoggedOut(state) {
      localStorage.removeItem("accessToken");
      localStorage.removeItem("userObject");
      state.commit("wipeUserTokenObject");
    }
  },
  modules: {}
});
